}

.nav__link {
  padding-bottom: 3px;
  position: relative;
}

.nav__link:before {
  content: "";
  transform-origin: 100%;
  background-color: #747373;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  transition: transform .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale(0);
}

.nav__link--active {
  padding-bottom: 3px;
  position: relative;
}

.nav__link--active:before {
  content: "";
  background-color: #747373;
  border-radius: 4px;
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover:before {
  transform-origin: 0;
  transform: scale(1);
}

